import returns from '../../common/returns.vue'
export default {
	components: {
		returns
	},
	data() {
		return {
			title: '订单详情',
			data: '',
			data_list: '',
			pay_list: '',
			show: false,
			payment_id: '',
			payment_name: '',

		}
	},
	methods: {
		jump(url) {
			uni.navigateTo({
				url: url
			})
		},
		determine(){//确定收货
			let that = this
			uni.showModal({
			    title: '提示',
			    content: '是否确定收货？',
			    success: function (res) {
			        if (res.confirm) {
			            that.service.entire(that,'post',that.APIconfig.api_root.subuser.threeuser.s_order_collect,{
							id:that.data.id,
							user_id: that.$store.state.user.id,
						},function(self,res){
							uni.showToast({
								icon:'none',
								title:res.msg
							})
			            	if(res.code == 0){
								setTimeout(function(){
									self.service.returns()
								},1500)
							}
			            })
			        } else if (res.cancel) {
			           return
			        }
			    }
			});
		},
		cancel_return(id){//取消退款申请
			let that = this
			console.log(id)
			uni.showModal({
			    title: '提示',
			    content: '是否取消退款/退货申请？',
			    success: function (res) {
			        if (res.confirm) {
			            that.service.entire(that,'post',that.APIconfig.api_root.subuser.threeuser.s_cancel,{
							id:id,
							user_id: that.$store.state.user.id,
						},function(self,res){
							uni.showToast({
								icon:'none',
								title:res.msg
							})
			            	if(res.code == 0){
								setTimeout(function(){
									self.service.returns()
								},1000)
							}
			            })
			        } else if (res.cancel) {
			           return
			        }
			    }
			});
		},
		cancel(){//取消订单
			let that = this
			uni.showModal({
			    title: '提示',
			    content: '是否确定取消？',
			    success: function (res) {
			        if (res.confirm) {
			            that.service.entire(that,'post', that.APIconfig.api_root.subuser.threeuser.s_order_cancel,{
							id:that.data.id,
							user_id: that.$store.state.user.id,
						},function(self,res){
							uni.showToast({
								icon:'none',
								title:res.msg
							})
			            	if(res.code == 0){
								setTimeout(function(){
									self.service.returns()
								},1500)
							}
			            })
			        } else if (res.cancel) {
			           return
			        }
			    }
			});
			
			
		},
		
		choice(index) {
			for (let s of this.pay_list) {
				s.choice = false
			}
			this.pay_list[index].choice = true
			this.pay_list = JSON.parse(JSON.stringify(this.pay_list))
			this.payment_id = this.pay_list[index].id
			this.payment_name = this.pay_list[index].payment
		},
		payment() {
			//提交
			let  that = this
			if (!this.payment_id) {
				uni.showToast({
					icon: 'none',
					title: '请选择支付方式'
				})
				return
			}
			uni.showModal({
			    title: '提示',
			    content: '是否确定支付？',
			    success: function (res) {
			        if (res.confirm) {
			            that.service.entire(that,'post',that.APIconfig.api_root.com_page.order_pay, {
			            	user_id: that.$store.state.user.id,
			            	id: that.data.id,
			            	payment_id: that.payment_id
			            }, function(self, ref) {
			            	self.service.order(ref,self,'../s_order?status=-1','pages/subuser/s_order?status=-1')
			            	
			            })
			        } else if (res.cancel) {
			           return
			        }
			    }
			});
			

		}
	},
	onLoad(options) {
		this.service.entire(this, 'post', this.APIconfig.api_root.subuser.threeuser.s_order_detail, {
			id: options.id,
			user_id: this.$store.state.user.id,
		}, function(self, res) {
			self.data = res.data
			self.data_list = res.data.items
			if(res.data.payment_list){
				let data = res.data.payment_list
				for (let s of data) {
					s.choice = false
				}
				self.pay_list = data
			}
			
			
		})
	}
}
